<template>
  <uv-button
    :customStyle="{ ...commonStyle, ...customStyle }"
    :disabled="disabled"
    :loading="loading"
    :loadingText="loadingText"
    @click="click"
    v-bind="$attrs"
    :throttleTime="300"
  >
    <slot></slot>
  </uv-button>
</template>

<script>
export default {
  options: {
    styleIsolation: 'shared', // 解除样式隔离
  },
  props: {
    loadingText: {
      type: String,
      default: '',
    },
    loading: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    customStyle: {
      type: Object,
      default: () => {
        return {}
      },
    },
  },
  data() {
    return {}
  },
  computed: {
    commonStyle() {
      let obj = {
        height: '80rpx',
        'font-size': '28rpx',
        color: '#fff',
        // background: 'linear-gradient(227.21deg, #1b8bff 0%, #6ad1ff 100%)',
        'border-radius': '200rpx',
        'line-height': '80rpx',
        'border-style': 'none',
      }
      if (this.disabled) {
        obj.background = 'rgba(106, 209, 255, 1)'
        obj.color = '#fff'
      }
      return obj
    },
  },
  methods: {
    click(e) {
      console.log('--------button')
      this.$emit('customClick', e)
      this.$emit('click', e)
    },
  },
}
</script>

<style lang="scss">
::v-deep .uv-button-wrapper {
  width: 100%;
}
button {
  display: block;
  width: 100%;
  height: 100%;
}
</style>
